.btn {
  display: inline-flex;
  align-items: center;
  font-size: 1.125em;
  font-weight: 700;
  font-family: "Didot";
  line-height: 1.555em;
  padding: 0.722em 1.388em;
  user-select: none;
  border: 0;
  outline: 0;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
  border-radius: 30em;
  color: #2E2E2E;
  background-color: transparent;
  position: relative;
}
@media (max-width:575.98px) {
  .btn {
    font-size: 1.1em;
  }
}
.btn:focus {
  outline: none;
}
.btn:hover {
  box-shadow: 0 7px 4px -5px rgba(0, 0, 0, 0.5);
}
.btn--primary {
  color: #ffffff;
  background: #FF7C46;
}
.btn--primary:hover {
  background: #2E2E2E;
}
.btn--secondary {
  background: #2E2E2E;
  color: #ffffff;
}
.btn--secondary:hover {
  background: #FF7C46;
}
.btn--icon {
  padding-left: 3.055em;
}
.btn--icon::before {
  content: "";
  position: absolute;
  background: url("../svg/right-icon.svg") center/contain no-repeat;
  width: 1.05em;
  height: 0.72em;
  left: 1.2222em;
  transition: all 0.3s ease-in-out;
  transform: rotate(180deg);
}
.btn--icon:hover::before {
  transform: rotate(220deg);
}
.btn--white {
  color: #2E2E2E;
  background-color: #ffffff;
}
.btn--white:hover {
  color: #ffffff;
  background: #FF7C46;
}
.btn.active {
  color: #ffffff;
  background: #FF7C46;
}
.btn--circle-icon {
  padding: 0.1666em 1.111em;
}
.btn--circle-icon .round-btn {
  width: 3.166em;
  height: 3.166em;
  flex: 0 0 3.166em;
  margin-right: 1.777em;
}
.btn--circle-icon:hover {
  box-shadow: none;
}
.btn--circle-icon:hover .round-btn {
  background-color: #2E2E2E;
}
.btn--circle-icon:hover img,
.btn--circle-icon:hover svg {
  transform: rotate(45deg);
}
.btn--gray {
  color: #616364;
  border: 1px solid #CCCCCC;
  text-transform: capitalize;
  padding: 0.4em 1em;
}
.btn--gray:hover {
  color: #ffffff;
  background-color: #616364;
}
.btn--gray.active {
  color: #ffffff;
  background-color: #616364;
}

.uline {
  position: relative;
  text-decoration: none;
  display: inline-block;
}
.uline::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -0.125em;
  width: 100%;
  height: 1px;
  background: #2E2E2E;
  transform: scaleX(1);
  transition: transform 0.5s ease;
}
.uline:hover::after, .uline.active::after {
  transition: transform 0.5s ease;
  transform: scaleX(0);
}

.w-uline {
  position: relative;
  text-decoration: none;
  display: inline-block;
}
.w-uline::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -0.19em;
  width: 100%;
  height: 2px;
  background: #ffffff;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}
.w-uline:hover, .w-uline.active {
